* {
  box-sizing: border-box;
}
/* Universal */
button {
  outline: none;
  border: 0;
  cursor: pointer;
}

/* Nav */
.nav {
  display: flex;
  text-align: center;
  align-self: center;
  background-color: bisque;
  padding: 1em 2em;
}
.logo {
  color: green;
}
.nav__logo {
  font-size: 2.5rem;
  padding-right: 0.5em;
}
.nav__title {
  font-size: 2.5rem;
}
.nav__count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: green;
  color: white;
  border-radius: 50%;
}

/* Input*/
.input {
  display: flex;
  padding: 1em 2em 0 2em;
}
.input__box {
  width: 100%;
  font-size: 2rem;
}

.input__btn {
  background-color: green;
  color: white;
  font-size: 1.5rem;
  margin-left: 0.5em;
  padding: 0.5em;
}
/* Habit Container */
.habit {
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}
.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

/* Habit Button */
.habit-button {
  font-size:  2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}
.habit-button:hover {
  opacity: 0.8;
}

/* Buttons */
.habit-increase,
.habit-decrease {
  color: goldenrod;
}
.habit-delete {
  color:brown;
}

/* Reset */
.reset {
  background-color: green;
  color: white;
  font-size: 1.5rem;
  margin-left: 2.5em;
  padding: 0.5em;
}